import logo from './logo.svg';
import './App.css';
import React from 'react';
import ReactDOM from 'react-dom';



class Nav extends React.Component {


  renderNavLink(name) {
    return (
      <button> {name} </button>
    )
  }

  renderMedia(img, alt, href) {
    return (

      <a href={href} target="_blank" rel="noopener noreferrer">
        <img src={img} alt={alt} />
      </a>



    )
  }

  render(){
    return (
      <div className="nav">
        <div className="wordmark">
          Quinn Voronin
        </div>
        <hr />
        <div className="navLinks">
          {this.renderNavLink("Skills")}
          {this.renderNavLink("About")}
          {this.renderNavLink("Work")}
        </div>

        <div className="bottom">
          <div className="media">
            {this.renderMedia("GitHub-Mark-Light-64px.png", "github", "https://github.com/buinton")}
            {this.renderMedia("instagram-light-64px.png", "instagram", "https://www.instagram.com/quinnvoronin/")}
          </div>
          <div className="contact-me">
            contact me
          </div>

        </div>
      </div>
    )
  }
}




class Landing extends React.Component {

  renderTitle() {
    return (
      <div className="title">
        <div className="name">
          Quinn Voronin
        </div>
        <div className="job">
          inventor
        </div>
        <div className="tagline">
          building solutions for the future
        </div>
        <button className="seemore">
          see more
        </button>
      </div>
    );
  }


  render() {
    return (
      <div className="landing">
        {this.renderTitle()}

        <img src="boys.png" alt="Quinn with 3 copies of himself looking into the camera"/>
      </div>
    )
  }
}







class Content extends React.Component {


  // Responsible for rendering the main content field.
  constructor(props) {
    super(props);
    this.state = {
      currentMedia: <Landing />,
    };
  }

  render() {
    return (
      <div className="content">
        {this.state.currentMedia}
      </div>
    )
  }
}


function App() {
  return (

    <div className="app">
      <div className="nav">
        <Nav />
      </div>
      <Content />
    </div>
  );
}

export default App;
